    "<p>You have not installed an Info Adapter. The adapter shows you information about the system and is required to display important messages from the ioBroker team.</p><p>Do you want to install the info adapter?</p>": {"en": "<p>You have not installed an Info Adapter. The adapter shows you information about the system and is required to display important messages from the ioBroker team.</p><p>Do you want to install the info adapter?</p>", "de": "<p>Sie haben keinen Info Adapter installiert. Der Adapter zeigt Ihnen Informationen zum System und ist erforderlich, um wichtige Meldungen des ioBroker-Teams anzuzeigen.</p><p>Mchten Sie den Info-Adapter installieren?</p>", "ru": "<p>    .               ioBroker.</p><p>   ?</p>", "pt": "<p>Voc no instalou um adaptador de informaes. O adaptador mostra informaes sobre o sistema e  necessrio para exibir mensagens importantes da equipe do ioBroker.</p><p>Deseja instalar o adaptador de informaes?</p>", "nl": "<p>U hebt geen Info-adapter genstalleerd. De adapter geeft informatie over het systeem weer en is nodig om belangrijke berichten van het ioBroker-team weer te geven.</p><p>Wilt u de infolader installeren?</p>", "fr": "<p>Vous navez pas install dInfo Adapter. Ladaptateur affiche des informations sur le systme et est ncessaire pour afficher les messages importants de lquipe ioBroker.</p><p>Souhaitez-vous installer ladaptateur dinformations?</p>", "it": "<p>Non hai installato un adattatore informazioni. L'adattatore mostra le informazioni sul sistema ed  necessario per visualizzare messaggi importanti dal team ioBroker.</p><p>Vuoi installare l'adattatore di informazioni?</p>", "es": "<p>No ha instalado un adaptador de informacin. El adaptador le muestra informacin sobre el sistema y debe mostrar mensajes importantes del equipo de ioBroker.</p><p>Desea instalar el adaptador de informacin?</p>", "pl": "<p>Nie masz zainstalowanego adaptera Info. Adapter wywietla informacje o systemie i jest wymagany do wywietlania wanych wiadomoci od twrcw ioBroker.</p><p>Czy chcesz zainstalowa adapter Info?</p>", "zh-cn": "<p>ioBroker</p><p></p>"},
    "<p>Your version of the info adapter is outdated. It is strongly recommended to update this to get on the enjoyment of the innovations. Only with the new version, for example, messages from the ioBroker team can be displayed directly.</p><p>Would you like to update the info adapter?</p>": {"en": "<p>Your version of the info adapter is outdated. It is strongly recommended to update this to get on the enjoyment of the innovations. Only with the new version, for example, messages from the ioBroker team can be displayed directly.</p><p>Would you like to update the info adapter?</p>", "de": "<p>Ihre Version des Info-Adapters ist veraltet. Es wird dringend empfohlen, dies zu aktualisieren, um die Neuerungen genieen zu knnen. Nur mit der neuen Version knnen beispielsweise Meldungen des ioBroker-Teams direkt angezeigt werden.</p><p>Mchten Sie den Info-Adapter aktualisieren?</p>", "ru": "<p>    .    ,     . ,        ioBroker   .</p><p>   ?</p>", "pt": "<p>Sua verso do adaptador de informaes est desatualizada.  altamente recomendvel atualizar isso para aproveitar o prazer das inovaes. Somente com a nova verso, por exemplo, mensagens da equipe do ioBroker podem ser exibidas diretamente. </ P> <p> Voc gostaria de atualizar o adaptador de informaes?</p>", "nl": "<p>Uw versie van de infolader is verouderd. Het wordt ten zeerste aanbevolen om dit bij te werken om te genieten van de innovaties. Alleen met de nieuwe versie kunnen berichten van het ioBroker-team bijvoorbeeld direct worden weergegeven.</p><p>Wilt u de infolader bijwerken?</p>", "fr": "<p>Votre version de ladaptateur dinformations est obsolte. Il est fortement recommand de le mettre  jour pour profiter pleinement des innovations. Par exemple, avec la nouvelle version uniquement, les messages de lquipe ioBroker peuvent tre affichs directement.</p><p>Souhaitez-vous mettre  jour ladaptateur dinformations?</p>", "it": "<p>La tua versione dell'adattatore informazioni non  aggiornata. Si consiglia vivamente d'aggiornarla, per ottenere sempre le novit. Solo con la nuova versione, ad esempio, i messaggi del team ioBroker possono essere visualizzati direttamente.</p><p>Vuoi aggiornare l'adattatore informazioni?</p>", "es": "<p>Su versin del adaptador de informacin no est actualizada. Se recomienda encarecidamente actualizar esto para disfrutar de las innovaciones. Solo con la nueva versin, por ejemplo, los mensajes del equipo ioBroker se pueden mostrar directamente.</p><p>Desea actualizar el adaptador de informacin?</p>", "pl": "<p>Twoja wersja adaptera Info jest nieaktualna. Zalecana jest aktualizacja, aby korzysta z nowych funkcji. Tylko dziki nowej wersji moliwe jest np. wywietlanie bezporednio wiadomoci od twrcw ioBroker.</p><p>Czy chcesz zaktualizowa adapter Info?</p>", "zh-cn": "<p>ioBroker</p><p></p>"},
