    "diag-note": {                                   "en": "Help us to make ioBroker even better! <br> Allow the transmission of anonymous usage statistics and crash reports. The exact scope of the data sent to iobroker.org when updating the adapter list can be selected below and is shown in the overview on the right. The data does not contain any personal information and helps us to set the priorities for the further development of ioBroker in the interest of all users and to be able to correct occurring errors quickly.<br>Much thanks for supporting our hard work by agreeing to the transmission of the statistical data!", "de": "Helfen Sie uns, ioBroker noch besser zu machen!<br>Gestatten Sie die bermittlung anonymer Nutzungs-Statistiken und Absturzmeldungen. Den genauen Umfang der bei einer Aktualisierung der Adapterliste an iobroker.org gesendeten Daten ist unten whlbar und wird rechts in der bersicht dargestellt. Die Daten enthalten keine persnlichen Informationen und helfen uns die Prioritten fr die Weiterentwicklung von ioBroker im Interesse aller Nutzer richtig zu setzen und auftretende Fehler schnell beheben zu knnen.<br>Vielen Dank fr die Untersttzung unserer harten Arbeit durch die Zustimmung zur bermittlung der Statisktikdaten!", "ru": "   ioBroker  ! <br>         .   ,   iobroker.org    ,         .               ioBroker           . <br>       ,     !", "pt": "Ajude-nos a tornar o ioBroker ainda melhor! <br> Permitir a transmisso de estatsticas de uso annimas e relatrios de falhas. O escopo exato dos dados enviados ao iobroker.org ao atualizar a lista de adaptadores pode ser selecionado abaixo e  mostrado na viso geral  direita. Os dados no contm informaes pessoais e nos ajudam a definir as prioridades para o desenvolvimento futuro do ioBroker no interesse de todos os usurios e para poder corrigir rapidamente os erros que ocorrem. <br> Muito obrigado por apoiar nosso trabalho rduo, concordando com a transmisso dos dados estatsticos!", "nl": "Help ons om ioBroker nog beter te maken! <br> Sta de verzending van anonieme gebruiksstatistieken en crashrapporten toe. De exacte omvang van de gegevens die naar iobroker.org worden gestuurd bij het bijwerken van de adapterlijst, kan hieronder worden geselecteerd en wordt weergegeven in het overzicht aan de rechterkant. De gegevens bevatten geen persoonlijke informatie en helpen ons om prioriteiten te stellen voor de verdere ontwikkeling van ioBroker in het belang van alle gebruikers en om snel voorkomende fouten te kunnen corrigeren. <br> Hartelijk dank voor uw steun aan ons harde werk door in te stemmen met de verzending van de statistische gegevens!", "fr": "Aidez-nous  rendre ioBroker encore meilleur! <br> Autorisez la transmission de statistiques d'utilisation anonymes et de rapports de plantage. L'tendue exacte des donnes envoyes  iobroker.org lors de la mise  jour de la liste des adaptateurs peut tre slectionne ci-dessous et est indique dans l'aperu  droite. Les donnes ne contiennent aucune information personnelle et nous aident  dfinir les priorits pour le dveloppement ultrieur d'ioBroker dans l'intrt de tous les utilisateurs et pour pouvoir corriger rapidement les erreurs qui se produisent. <br> Merci beaucoup d'avoir soutenu notre travail acharn en acceptant la transmission des donnes statistiques!", "it": "Aiutaci a rendere ioBroker ancora migliore! <br> Consentire la trasmissione di statistiche anonime sull'utilizzo e rapporti sugli arresti anomali. L'ambito esatto dei dati inviati a iobroker.org durante l'aggiornamento dell'elenco degli adattatori pu essere selezionato di seguito e viene mostrato nella panoramica a destra. I dati non contengono alcuna informazione personale e ci aiutano a stabilire le priorit per l'ulteriore sviluppo di ioBroker nell'interesse di tutti gli utenti e per poter correggere rapidamente gli errori che si verificano. <br> Mille grazie per aver supportato il nostro duro lavoro accettando la trasmissione dei dati statistici!\"", "es": "Aydanos a mejorar ioBroker! <br> Permitir la transmisin de estadsticas de uso annimas e informes de fallos. El alcance exacto de los datos enviados a iobroker.org al actualizar la lista de adaptadores se puede seleccionar a continuacin y se muestra en la descripcin general a la derecha. Los datos no contienen ninguna informacin personal y nos ayudan a establecer las prioridades para el desarrollo posterior de ioBroker en inters de todos los usuarios y para poder corregir los errores que ocurren rpidamente. <br> Muchas gracias por apoyar nuestro arduo trabajo al aceptar la transmisin de los datos estadsticos!", "pl": "Pom nam ulepszy ioBroker! <br> Zezwl na przesyanie anonimowych statystyk uytkowania i raportw o awariach. Dokadny zakres danych wysyanych do iobroker.org podczas aktualizacji listy adapterw mona wybra poniej i pokazano w przegldzie po prawej stronie. Dane nie zawieraj adnych danych osobowych i pomagaj nam ustali priorytety dla dalszego rozwoju ioBroker w interesie wszystkich uytkownikw i pozwalaj nam szybko naprawi wystpujce bdy. <br> Ogromne dziki za wsparcie naszej cikiej pracy, wyraajc zgod na przesyanie danych statystycznych!", "zh-cn": "ioBroker <br>iobroker.orgioBroker <br>"},
